import React, { Component } from 'react'
import TableCmp from '../../@components/TableCmp';
import { NavLink } from 'react-router-dom';
import Busy from '../../@components/Busy';
import ConfigService from '../../@services/Config';

import { Pane, Heading, Button, AddIcon, EditIcon, Badge, PlusIcon, Text, TickIcon, CrossIcon } from 'evergreen-ui'

class PageConfig__SchemaList extends Component {

  constructor() {
    super()
    this.state = {
      schemaLabel: '...',
      tableCols: [
        {label:'Key', key: 'key'},
        // {label:'Description', key: 'description'}
      ],
      tableData: [],
      supportsTemplates: false,
      ready: false
    }
    this.mounted = false
  }
  
  componentDidMount()
  {
    
    this.mounted = true
    
    this.updateTableData();

  }

  componentWillUnmount()
  {
    
    this.mounted = false

  }

  async updateTableData() {
    
    if (!this.mounted) return;
    
    ConfigService.getSchema(this.props.schemaKey)
      .then(({ documentation, schema, supportsTemplates, table }) => {

        ConfigService.getValue(this.props.schemaKey)
          .then(SchemaValue => {

            var tableCols = table.columns;

            var tableData = [];

            // run through each result, populate table columns
            SchemaValue.forEach((row, index) => {

              var tableRowData = {}

              table.columns.forEach(column => {
                tableRowData[column.key] = (() => {
                  if (undefined === row[column.key]) return;
                  switch (column.type) {
                    case 'key':
                      return (<NavLink key={row[column.key]} to={`${this.props.match.url}/${row.key}`}><Button appearance="minimal" iconBefore={EditIcon}>{row[column.key]}</Button></NavLink>);
                    case 'boolean':
                      return (row[column.key] === true ? (<TickIcon/>) : (<CrossIcon/>));
                    case 'dependency[]':
                      return (row[column.key].map((item, index) => {
                        // TODO: add "is internal" check, and link to source.
                        return (<Badge key={index} color="neutral" marginRight={3}>{typeof item === "string" ? (item) : (`${item.id}@${item.version}`)}</Badge>)
                      }))
                    case 'string[]':
                      return (<Text>{row[column.key].join(', ')}</Text>);
                    default:
                      return (<Text>{row[column.key].toString()}</Text>);
                  }
                })()
              })

              tableData.push(tableRowData)

            })
        
            if (!this.mounted) return;
  
            this.setState({
              supportsTemplates: supportsTemplates || false,
              tableCols: tableCols,
              tableData: tableData,
              ready: true
            });

          })

      })
    
  }

  render() {
    console.log(this.props.match)
    return (
      <article>
        <Pane display="flex" padding=".5em" background="tint2">
          <Pane flex={1} alignItems="center" display="flex">
            <Heading size={600}>{this.props.schemaKey}</Heading>
          </Pane>
          <Pane>
            {this.state.supportsTemplates?(
              <NavLink to={`/${this.props.match.params.siteKey}/templates/${this.props.schemaKey}`}><Button appearance="minimal" iconBefore={AddIcon}>Add Template</Button></NavLink>
            ):null}
            <NavLink to={`${this.props.match.url}/new`}><Button appearance="minimal" iconBefore={PlusIcon}>Add New</Button></NavLink>
          </Pane>
        </Pane>
        <Pane>
          {
            (this.state.ready) ?
            (<TableCmp emptyMsg={`No ${this.props.schemaKey}`} cols={this.state.tableCols} rows={this.state.tableData} />) :
            <Busy/>
          }
        </Pane>
      </article>
    )
  }
}

export default PageConfig__SchemaList;